:root{
  --primary: #3A78F2;
  --light-blue:#a4bbe2;
  --yellow: #FFAF02;
  --light-yellow: #FFD006;
  --green-water :#1ED796; 
  --light-gray: #F5F7FB;
  --gray: #7f7f81;
  --light-green: #52c669;
  --dark-gray: #404043;
  --light-red:#e4696b;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  user-select: none;
}
*:focus-visible{
  outline: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root > .App{
  display: flex;
  height: 100vh;    
  overflow: hidden;
  width: 100vw;
  background-color: #fff;
}

#root > .App > .app__ctn{
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

button{
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ol, ul{
  list-style: none;
}

a:link{
  text-decoration: none;
}

input{
  outline: none;
  border: none;
}
input:focus{
  outline: none;
}

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #ffffff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(190, 190, 190);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(141, 141, 141);
}


/* ANIMATIONS */

:root {
  --animate-duration: .4s;
  --animate-delay: 0;
  --animate-repeat: 1;
}

.animated {
  animation-duration: var(--animate-duration);
  animation-fill-mode: both;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.5, 0.5, 0.5);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  animation-name: zoomIn;
}

@keyframes zoomOut {
  from {
    opacity: 1;
  }

  50% {
    opacity: 0;
    transform: scale3d(0.5, 0.5, 0.5);
  }

  to {
    opacity: 0;
  }
}

.zoomOut {
  animation-name: zoomOut;
}