.latest-transactions__ctn{
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #fff;

}

.latest-transactions__header{
  padding: 6px 0;
  text-align: center;
  background-color: var(--yellow);
  color: #fff;
  font-weight: 500;
}
.latest-transactions__list{
  flex: 1 0 ;
  flex-basis: 0;
  overflow: auto;
  width: max-content;
}

.latest-transactions__day{
  padding: 8px 0;
  background-color: var(--primary);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  text-align: center;
  font-weight: 500;
  color: #fff;
}


.latest-transactions__day-list{
  display: flex;
  flex-direction: column;
}

.latest-transactions__item{
  display: flex;  
  align-items: center;
  gap: 15px;
  padding: 9px 20px;
}

.latest-transactions__item:nth-child(even){
  background-color: #fff;
} 

.latest-transactions__item:nth-child(odd){
  background-color: var(--light-gray);
} 

.latest-transactions__item:hover{
  background-color: #e5e5e5;
}

.latest-transactions__item.income{
  border-left: 2px solid var(--light-green);
}
.latest-transactions__item.expense{
  border-left: 2px solid var(--light-red);
}


.latest-transactions__item-info{
}
.latest-transactions__item-local{
  font-weight: 600;
  font-size: 16px;
}
.latest-transactions__item-time{
  font-size: 12px;
}



.latest-transactions__item-amount{
  font-size: 14px;
  font-weight: 500;
  margin-left: auto;
}

.latest-transactions__item.income .latest-transactions__item-amount{
  color: var(--light-green);
}

.latest-transactions__item.expense .latest-transactions__item-amount{
  color: var(--light-red);
}